<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 150px;" placeholder="请输入交易订单"
                      @keyup.native.enter="getList"
                      v-model="matchQuery.orderId">
            </el-input>
            <el-input class="filter-item" style="width: 150px;" placeholder="请输入广告编号"
                      @keyup.native.enter="getList"
                      v-model="matchQuery.advertisingID">
            </el-input>
            <el-input class="filter-item" style="width: 150px;" placeholder="请输入买家信息"
                      @keyup.native.enter="getList"
                      v-model="matchQuery.buyerUser">
            </el-input>
            <el-input class="filter-item" style="width: 150px;" placeholder="请输入卖家信息"
                      @keyup.native.enter="getList"
                      v-model="matchQuery.sellerUser">
            </el-input>
            <span style="margin-left: 20px;margin-right: 10px">状态：</span>
            <el-select clearable style="width: 150px" class="filter-item"
                       v-model="matchQuery.state">
                <el-option v-for="typeStatus in statusOptions" :key="typeStatus.key"
                           :label="typeStatus.status_name"
                           :value="typeStatus.key">
                </el-option>
            </el-select>
            <span style="margin-left: 20px;margin-right: 10px">虚拟币：</span>
            <el-select clearable style="width: 150px" class="filter-item"
                       v-model="matchQuery.coinID">
                <el-option v-for="typeStatus in cionlist" :key="typeStatus.key"
                           :label="typeStatus.englishName"
                           :value="typeStatus.baseSeq">
                </el-option>
            </el-select>
            <el-button class="filter-item" @click="typeQuery" size="small">查询</el-button>
            <el-button type="primary" @click="handleDialogShow('add')" v-if="permission.add" size="small">新增</el-button>
        </div>

        <el-table
              class="m-tradeorderTable"
              ref="singleTable"
              :data="tableData"
              style="width: 100%">
            <el-table-column type="expand">
                <template scope="props">

                    <div class="m-detailPanel">

                      <div class="m-detailPanel-item buy">
                        <span class="title">买方信息</span>
                        <ul>
                          <li>
                            <span class="m-detailPane-label">用户UID</span>
                            <span class="m-detailPane-content">{{ props.row.buyerUser }}</span>
                          </li>
                          <li>
                            <span class="m-detailPane-label">昵称</span>
                            <span class="m-detailPane-content">{{ props.row.buyeralaisName }}</span>
                          </li>
                          <li>
                            <span class="m-detailPane-label">真实姓名</span>
                            <span class="m-detailPane-content">{{ props.row.buyerrealname }}</span>
                          </li><li>
                            <span class="m-detailPane-label">电话号码</span>
                            <span class="m-detailPane-content">{{ props.row.buyerphone }}</span>
                          </li>
                          <li>
                            <span class="m-detailPane-label">邮箱地址</span>
                            <span class="m-detailPane-content">{{ props.row.buyeremail }}</span>
                          </li>
                          <li>
                            <span class="m-detailPane-label">类型</span>
                            <span class="m-detailPane-content">{{ props.row.buyerType }}</span>
                          </li>
                          <li>
                            <span class="m-detailPane-label">申诉号码</span>
                            <span class="m-detailPane-content">{{ props.row.buyerNum }}</span>
                          </li>
                        </ul>
                      </div>

                      <div class="m-detailPanel-item sell">
                        <span class="title">卖方信息</span>
                        <ul>
                          <li>
                            <span class="m-detailPane-label">用户UID</span>
                            <span class="m-detailPane-content">{{ props.row.sellerUser }}</span>
                          </li>
                          <li>
                            <span class="m-detailPane-label">昵称</span>
                            <span class="m-detailPane-content">{{ props.row.selleralaisName }}</span>
                          </li>
                          <li>
                            <span class="m-detailPane-label">真实姓名</span>
                            <span class="m-detailPane-content">{{ props.row.sellerrealname }}</span>
                          </li><li>
                            <span class="m-detailPane-label">电话号码</span>
                            <span class="m-detailPane-content">{{ props.row.sellerphone }}</span>
                          </li>
                          <li>
                            <span class="m-detailPane-label">邮箱地址</span>
                            <span class="m-detailPane-content">{{ props.row.selleremail }}</span>
                          </li>
                          <li>
                            <span class="m-detailPane-label">类型</span>
                            <span class="m-detailPane-content">{{ props.row.sellerType }}</span>
                          </li>
                          <li>
                            <span class="m-detailPane-label">申诉号码</span>
                            <span class="m-detailPane-content">{{ props.row.sellerNum }}</span>
                          </li>
                        </ul>
                      </div>

                      <div class="m-detailPanel-item order">
                        <span class="title">订单详情</span>
                        <ul>
                          <li>
                            <span class="m-detailPane-label">创建时间</span>
                            <span class="m-detailPane-content">{{ props.row.createTime }}</span>
                          </li>
                          <li>
                            <span class="m-detailPane-label">付款时间</span>
                            <span class="m-detailPane-content">{{ props.row.paymentTime }}</span>
                          </li>
                          <li>
                            <span class="m-detailPane-label">释放时间</span>
                            <span class="m-detailPane-content">{{ props.row.accomplishTime }}</span>
                          </li><li>
                            <span class="m-detailPane-label">取消时间</span>
                            <span class="m-detailPane-content">{{ props.row.cancelTime }}</span>
                          </li>
                          <li>
                            <span class="m-detailPane-label">申诉时间</span>
                            <span class="m-detailPane-content">{{ props.row.allegeTime }}</span>
                          </li>
                          <li>
                            <span class="m-detailPane-label">申诉方UID</span>
                            <span class="m-detailPane-content">{{ props.row.allegeUser }}</span>
                          </li>
                          <li>
                            <span class="m-detailPane-label">处理结果</span>
                            <span class="m-detailPane-content">{{ orderResult(props.row.state) }}</span>
                          </li>
                        </ul>
                      </div>

                    </div>

                    <!-- <el-form label-position="center" inline class="demo-table-expand">
                        <el-form-item label="买方信息"></el-form-item>
                        <el-form-item label="用户UID">
                            <span>{{ props.row.buyerUser }}</span>
                        </el-form-item>
                        <el-form-item label="昵称">
                            <span>{{ props.row.buyeralaisName }}</span>
                        </el-form-item>
                        <el-form-item label="真实姓名">
                            <span>{{ props.row.buyerrealname }}</span>
                        </el-form-item>
                        <el-form-item label="电话号码">
                            <span>{{ props.row.buyerphone }}</span>
                        </el-form-item>
                        <el-form-item label="邮箱地址">
                            <span>{{ props.row.buyeremail }}</span>
                        </el-form-item>
                        <el-form-item label="类型">
                            <span>{{ props.row.BuyerType }}</span>
                        </el-form-item>
                        <el-form-item label="申述号码">
                            <span>{{ props.row.buyerNum }}</span>
                        </el-form-item>
                    </el-form>
                    <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item label="卖方信息"></el-form-item>
                        <el-form-item label="用户UID">
                            <span>{{ props.row.sellerUser }}</span>
                        </el-form-item>
                        <el-form-item label="昵称">
                            <span>{{ props.row.selleralaisName }}</span>
                        </el-form-item>
                        <el-form-item label="真实姓名">
                            <span>{{ props.row.name }}</span>
                        </el-form-item>
                        <el-form-item label="电话号码">
                            <span>{{ props.row.sellerphone }}</span>
                        </el-form-item>
                        <el-form-item label="邮箱地址">
                            <span>{{ props.row.selleremail }}</span>
                        </el-form-item>
                        <el-form-item label="类型">
                            <span>{{ props.row.SellerType }}</span>
                        </el-form-item>
                        <el-form-item label="申述号码">
                            <span>{{ props.row.sellerNum }}</span>
                        </el-form-item>
                    </el-form>
                    <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item label="交易明细"></el-form-item>
                        <el-form-item label="创建时间">
                            <span>{{ props.row.createTime }}</span>
                        </el-form-item>
                        <el-form-item label="付款时间">
                            <span>{{ props.row.paymentTime }}</span>
                        </el-form-item>
                        <el-form-item label="释放时间">
                            <span>{{ props.row.accomplishTime }}</span>
                        </el-form-item>
                        <el-form-item label="取消时间">
                            <span>{{ props.row.cancelTime }}</span>
                        </el-form-item>
                        <el-form-item label="申述时间">
                            <span>{{ props.row.allegeTime }}</span>
                        </el-form-item>
                        <el-form-item label="申述方UID">
                            <span>{{ props.row.allegeUser }}</span>
                        </el-form-item>
                        <el-form-item label="处理结果">
                            <span>{{ props.row.result }}</span>
                        </el-form-item>
                    </el-form> -->


                </template>
            </el-table-column>
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>

            </el-table-column>

        </el-table>
        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="matchQuery.page"
                           :page-sizes="[10,20,30,50]" :page-size="matchQuery.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    </div>
</template>


<script>

    import fetch from '@/utils/fetch';
    import DCTP from '@/utils/dctp';
    import {getTradeOrderList} from '@/api/otcdealorder';
    import {virtualCoinAndGoods} from '@/api/virtual';
    import calc from "@/utils/calculate-big-num";

    const statusOptions = [
        {key: 0, status_name: '全部状态'},
        {key: 1, status_name: '等待支付'},
        {key: 2, status_name: '已支付,未放行'},
        {key: 3, status_name: '已完成'},
        {key: 4, status_name: '已取消'},
        {key: 5, status_name: '申诉中'},
    ];

    export default {
        data() {
            return {
                total: null,
                itemWidth: '160px',
                dialogVisible: false,
                feesDialogVisible: false,
                tableData: [],
                cionlist: null,
                matchQuery: {
                    orderId: '',
                    advertisingID: '',
                    buyerUser: '',
                    sellerUser: '',
                    state: '',
                    coinID: '',
                    page: 1,
                    pageSize: 10
                },
                statusOptions,
                action: '',
                tableHeader: [
                    {
                        label: "订单编号",
                        prop: "id",
                        width: 180
                    }, {
                        label: "广告",
                        prop: "advertisingID",
                        width: 160,

                    }, {
                        label: "类型",
                        prop: "type",
                        width: 120,
                        formatter(row, column, value) {
                            return value === 1 ? '买入' : (value === 2 ? '卖出' : '');
                        }
                    }, {
                        label: "状态",
                        prop: "state",
                        width: 120,
                        formatter(row, column, value) {
                            let item = DCTP.utils.findDataByValue(statusOptions, value, 'key');
                            return item ? item.status_name : '';
                        }
                    }, {
                        label: "购买方",
                        prop: "buyerUser",
                    }, {
                        label: "出售方",
                        prop: "sellerUser",
                    }, {
                        label: "资产名称",
                        prop: "enname",

                    }, {
                        label: "单价",
                        prop: "price",
                        formatter(row, column, value) {
                            return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                        }
                    }, {
                        label: "数量",
                        prop: "num",
                        formatter(row, column, value) {
                            return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                        }
                    }, {
                        label: "手续费",
                        prop: "fees",
                        formatter(row, column, value) {
                            return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                        }
                    }, {
                        label: "总价",
                        prop: "total",
                        formatter(row, column, value) {
                            return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                        }
                    }, {
                        label: "创建时间",
                        prop: "createTime",
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }
                ],
            }
        },
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        },

        methods: {

            orderResult( val ) {
              var str
              switch( val ){
                case 1:
                  str = '等待支付'
                break
                case 2:
                  str = '已付款,待放行'
                break
                case 3:
                  str = '已完成'
                break
                case 4:
                  str = '已取消'
                break
                case 5:
                  str = '申述中'
                break
                default:
                  str = ''
              }
              return str
            },

            typeQuery() {
                this.getlist()
            },
            getlist() {
                getTradeOrderList(this.matchQuery).then(response => {
                    if (!response.result) {
                        this.listLoading = false;
                    } else {
                        this.tableData = response.data.list;
                        this.total = response.data.total;
                        this.listLoading = false;
                    }
                })
            },
            //翻页的按钮
            userPage(val) {
                this.matchQuery.page = val;
                this.getlist();
            },
            //获取每页多少条
            userPageSize(val) {
                this.matchQuery.pageSize = val;
                this.getlist();
            },
            getcoinlist() {
                let _this = this;
                virtualCoinAndGoods([1,3], 2).then(response => {
                    if (response.result) {
                        _this.cionlist = response.data;
                    }
                })
            }
        },
        created() {
            this.getlist();
            this.getcoinlist();
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .input-short {
        width: 300px;
    }
    .input-fee {
        width: 90px;
    }


    .m-detailPanel{
      $c: #dfe6ec;
      border-radius: 5px;
      overflow: hidden;
      font-size: 0;
      border: {
        left: 1px solid $c;
        right: 1px solid $c;
        bottom: 1px solid $c;
      }
      .title{
        display: inline-block;
        width: 100/8 + %;
        font-size: 16px;
        line-height: 78px;
        border-right: 1px solid $c;
        border-top: 1px solid $c;
        text-align: center;
        vertical-align: top;
        background: #eef1f6;
      }
      .m-detailPane-label{
        color: #a7b5c7;
      }
      ul{
        box-sizing: border-box;
        display: inline-block;
        width: 100/8*7 + %;
        padding-left: 20px;
        padding-right: 20px;
        font-size: 12px;
        border-top: 1px solid $c;
        vertical-align: top;
        overflow: hidden;
        li{
          float: left;
          display: inline-block;
          width: 100/7 + %;
          height: 78px;
          line-height: 78px;
          white-space: nowrap;
          // text-overflow:ellipsis;
          overflow:hidden;
        }
      }


      .m-detailPanel-item.order{
        position: relative;
        .title{
          position: absolute;
          line-height: 83px;
          height: 100%;
        }
        ul{
          position: relative;
          left: 20px;
          width: 100%;
          padding-left: 100/8 + %;
          padding-top: 30px;
          padding-bottom: 30px;
          li{
            float: inherit;
            height: auto;
            line-height: inherit;
          }
        }
      }
    }

</style>



